@import "../../assets/scss/app";

.skeletonSpheres {

  .inner {
    position: relative;
    padding: 61px 16px 16px;

    @include desktop {
      padding: 89px 24px 24px;
      box-sizing: border-box;
      margin: 0 auto;
      width: 768px;
    }

    .texts {
      position: relative;
      z-index: 10;
      opacity: 0.5;
    }

    .wrapper {
      position: fixed;
      z-index: 1;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      background: #111;
    }

  }
}
