@import "../../assets/scss/app";

.ticTacToe {
  width: 100%;
  height: 100%;

  .panel {
    z-index: 2;
    position: fixed;
    top: 61px;
    left: 16px;

    @include desktop {
      top: 81px;
    }

    .selectFigure {
      display: block;
      margin-top: 16px;

      .zero {
        width: 32px;
        height: 32px;
        border-radius: 50%;
        border: 4px solid #000;
        box-sizing: border-box;
      }

      .cross {
        width: 32px;
        height: 32px;
        position: relative;

        &:before {
          position: absolute;
          content: "";
          display: block;
          width: 4px;
          height: 100%;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%) rotate(45deg);
          background: #000;
        }

        &:after {
          position: absolute;
          content: "";
          display: block;
          width: 4px;
          height: 100%;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%) rotate(-45deg);
          background: #000;
        }
      }
    }

    .winnerBlock {
      margin-top: 16px;

      .winner {
        text-transform: uppercase;
        font-weight: bold;
        color: dimgrey;

        &_user {
          color: blue;
        }

        &_pc {
          color: red
        }
      }
    }



  }
}