@import "../../assets/scss/app";

.home {
  padding-top: 45px;

  @include desktop {
    padding-top: 65px;
  }

  img {
    width: 100px;
    height: 100px;
  }

  .links {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding: 16px;

    @include desktop {
      padding: 24px;
    }

    .link {
      position: relative;
      display: flex;
      flex-direction: column;
      text-decoration: none;
      margin: 4px;
      width: 250px;
      height: 187px;
      border-radius: 5px;
      overflow: hidden;

      &:hover {

        img {
          @include onlyDesktop {
            transform: scale(1.05);
          }

        }

        p {
          @include desktop {
            opacity: 1;
          }
        }
      }

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        transition: 0.3s
      }

      p {
        position: absolute;
        left: 0;
        bottom: 0;
        color: #000;
        font-weight: bold;
        text-align: center;
        background: rgba(255, 255, 255, 0.75);
        padding: 4px 8px;
        border-radius: 0 5px 0 0;
        transition: 0.3s;
        opacity: 1;

        @include desktop {
          opacity: 0;
        }
      }


    }

  }
}
