@import "../../assets/scss/app";

.starSky {
  background: #000;

  .inner {
    position: relative;
    padding: 61px 16px 16px;

    @include desktop {
      padding: 89px 0 0;
      box-sizing: border-box;
      margin: 0 auto;
      width: 768px;
      overflow: hidden;
    }

    .texts {
      position: relative;
      z-index: 10;

      @include desktop {
        width: 768px;
        overflow: hidden;
      }

      p {
        color: #555;
        opacity: 0;
        user-select: none;
      }
    }

    .wrapper {
      position: fixed;
      z-index: 1;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
    }

  }
}
